












































import { Component, Vue } from "vue-property-decorator";

@Component
export default class NavComponent extends Vue {
  public pop: boolean = false;
  public scroll: boolean = false;
  public goHome(): void {
    this.$router.push({
      path: "/",
    });
  }

  public goHomeTopLabs(): void {
    if (this.$router.currentRoute.name === "Home") {
      location.href = "#homeAnchor";
      this.pop = false;
    } else {
      this.$router.push({
        path: "/",
      });
    }
  }

  public goHomeDspLabs(): void {
    if (this.$router.currentRoute.name === "Home") {
      location.href = "#firstAnchor";
      this.pop = false;
    } else {
      this.$router.push({
        path: "/",
        hash: "#firstAnchor",
      });
    }
  }

  public mounted() {
    document.addEventListener("scroll", (): void => {
      const scrollTop: number =
        document.documentElement.scrollTop || window.pageYOffset;
      if (scrollTop > 0) {
        this.scroll = true;
      } else {
        this.scroll = false;
      }
    });
  }

  public goGithub(): void {
    this.pop = false;
    window.open("https://github.com/DSP-Labs");
  }
}
